.skill {
  width: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 50px 10px 60px 10px;
  margin-bottom: 30px;
  box-shadow: 0 1px 5px rgba(5, 5, 6, 0.15);

  h5 {
    color: #757575;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    margin: 0;
    size: 24px;
  }

  hr {
    border: 0;
    border-top: 2px solid #dbdbdb;
  }

  p {
    font-family: 'Poppins', sans-serif;
    color: #979797;
    font-size: 14px;
    line-height: 24px;
    font-weight: 400;
    margin: 0;
  }

  .hR {
    margin: 15px auto;
    width: 50px;
  }

  .image {
    margin-bottom: 20px;
    border: 1px solid #8a8a8a;
    border-radius: 50%;
    color: #8a8a8a;
    font-size: 19px;
    height: 36px;
    line-height: 36px;
    width: 36px;

    .icon {
      font-size: 20px;
    }
  }
}









